import React, {Component, Fragment} from 'react';
import {Tab, Segment, Accordion, Icon} from 'semantic-ui-react';
import './accordion-list.css';
import ApiService from "../../services/api-service";
import accordion from "./accordion";
import ChartsStack from "../charts-stack";

class AccordionList extends Component {

    state = {
        data: [],
        loading: false,
        error: false
    };

    apiService = new ApiService();

    componentDidUpdate(prevProps) {
        if (this.props.nodeId !== prevProps.nodeId || this.props.tabId !== prevProps.tabId) {
            console.log("Accordion list new props", this.props );
            this.update(this.props.nodeId, this.props.tabId);
        }
    }

    componentDidMount() {
        console.log("Accordion list Mounted",this.props );
        const {nodeId, tabId} = this.props;
        this.update(nodeId, tabId);
    }

    update = (nodeId, id) => {
        this.setState({
            loading:true,
            // data: []
        });
        this.props.getList(nodeId, id)
            .then((data) => {
                this.setState({
                    loading:false,
                    data
                })
            })
            .catch((error)=>{
                this.setState({
                    loading: false,
                    error:true,
                    data: []
                });
            })
    };

    render() {
        const {loading, data} = this.state;
        const {suf, nodeId, tabId} = this.props;
        const elements = data.map((item, index) => {

            const {id, fio} = item;
            return (
                {
                    key: `acc_item_${id}`,
                    title: {
                        content: `${fio}`,
                        icon: 'angle right'
                    },
                    content: {
                        content: (
                            <ChartsStack className='charts-stack' nodeId={nodeId} secId={tabId} thId={id} type={suf}/>
                        ),
                    },
                }
            );
        });

        return (
            <Segment loading={loading} className="accordion-list-wrapper">
                <Accordion defaultActiveIndex={[]} exclusive={false} panels={elements} fluid/>
            </Segment>
        );
    }
};
export default AccordionList;