import React, { Component, Fragment } from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

import 'semantic-ui-css/semantic.min.css'
import ErrorBoundry from "../error-boundry";

import './chart.css';
import {DateFilter} from "../filters";
import ApiService from "../../services/api-service";
am4core.useTheme(am4themes_animated);

class ChartTemplate extends Component {

    state = {
        data: {},
        loading: true,
        from: this.props.from,
        to: this.props.to
    };

    apiService = new ApiService();

    componentDidMount() {
        const {id, oxName, oyName, lineName, ch_series } = this.props;
        this.chart = this.props.chartConstructor(id, oxName, oyName, ch_series, lineName);
        console.log(this.props.nodeId);
        console.log(this.props.id);
    }

    componentWillUnmount() {
        if (this.chart) {
            this.chart.dispose();
        }
    }

    updateChartData = ( from, to) => {
        const {nodeId, id} = this.props;
        this.setState({
            loading: true
        });
        console.log(`update from ${from} to ${to}`);
        this.apiService.updateStatisticCharts(nodeId, id, from, to)
            .then((info) => {
                this.chart.data = info.series.data;
                // this.chart.invalidateData();
                this.setState({
                      from: info.from,
                      to: info.to,
                      loading: false
                    })
            })
            .catch((error) => {
                console.log(error);
                this.setState({
                    loading: false
                })
            });
    };

    render() {
        const {title, id} = this.props;
        const {from, to, loading} = this.state;

        const divId = `chartdiv_${id}`;
        return (
            <ErrorBoundry>
                <div className="ui card" style={{ width: "100%" }}>
                    <div className="content" style={{display: 'flex', alignItems: 'center', justifyContent: 'safe center'}}>
                        <div className="header">{title}</div>
                        <DateFilter startDate={from} endDate={to} onSubmit={this.updateChartData} loading={loading}/>
                    </div>
                    <div className="content entry">
                        <div id={divId} className="chart"></div>
                        <div id="curtain"><span>Chart is loading...</span></div>
                    </div>
                </div>
            </ErrorBoundry>
        );
    }
};

export default ChartTemplate;

