import React, {Component, Fragment} from 'react';

import 'semantic-ui-css/semantic.min.css';
import './statistic-page.css';


export default class StatisticPage extends Component {

    state = {
        data: null,
        loading: true,
        error: false
    };

    componentDidUpdate(prevProps) {
        if (this.props.getData !== prevProps.getData) {
            this.update();
        }
    }

    componentDidMount() {
        this.update();
    }

    update() {
        this.setState( {
            loading: true,
            error: false
        });

        this.props.getData()
            .then((data) => {
                this.setState({
                    data,
                    loading: false
                });
            })
            .catch(() => {
                this.setState({
                    error: true,
                    loading: false
                });
            });
    }

    render() {
        const { data, loading } = this.state;
        return <div {...this.props} data={data} loading={loading}/>;
    };
};
