import React, {Component, Fragment} from 'react';
import {Confirm} from "semantic-ui-react";

import 'semantic-ui-css/semantic.min.css';

import './date-filter.css';

export default class DateFilter extends Component {

    _getYearFromStr = (date) => {
        return date ? + date.split('-')[0] : + new Date().getFullYear();
    };

    _getMonthFromStr = (date) => {
        return date ? + date.split('-')[1] : new Date().getMonth() + 1;
    };

    state = {
        startYear: this._getYearFromStr(this.props.startDate),
        endYear: this._getYearFromStr(this.props.endDate),
        startMonth: this._getMonthFromStr(this.props.startDate),
        endMonth: this._getMonthFromStr(this.props.endDate),
        oldStartDate: this.props.startDate,
        oldEndDate: this.props.endDate,
        startCalendarVisible: false,
        endCalendarVisible: false,
        locale: 'ru',
        monthArray: [],
        confirmOpen: false
    };

    componentDidMount () {
        const arr = this._locale[this.state.locale].months;
        this.setState({
            monthArray: arr
        });
    };

    componentDidUpdate(prevProps) {
        if (this.props.startDate !== prevProps.startDate || this.props.endDate !== prevProps.endDate ) {
            // console.log("не раввно!");
            this.setState({
                oldStartDate: this.props.startDate,
                oldEndDate: this.props.endDate
            });
        }
    }

    _locale = {
        ru: {
            months: [
                'Январь',
                'Февраль',
                'Март',
                'Апрель',
                'Май',
                'Июнь',
                'Июль',
                'Август',
                'Сентябрь',
                'Октябрь',
                'Ноябрь',
                'Декабрь'
            ],
            monthsShort: []
        }
    };

    _toggleCalendar = (type) => {
        switch (type) {
            case 'start':
                this.setState((state) => {
                    return {startCalendarVisible: !state.startCalendarVisible}
                });
                break;
            case 'end':
                this.setState((state) => {
                    return {
                        startCalendarVisible: false,
                        endCalendarVisible: !state.endCalendarVisible
                    }
                });
                break;
            case 'all':
                this.setState({
                        startCalendarVisible: false,
                        endCalendarVisible: false
                });
                break;
            default:
                break;
        }
    };

    _onPrevYear = (type) => {
        switch (type) {
            case 'start':
                this.setState((state) => {
                    return {startYear: state.startYear - 1}
                });
                break;
            case 'end':
                this.setState((state) => {
                    return {endYear: state.endYear - 1}
                });
                break;
            default:
                break;
        }
    };

    _onNextYear = (type) => {
        switch (type) {
            case 'start':
                this.setState((state) => {
                    return {startYear: state.startYear + 1}
                });
                break;
            case 'end':
                this.setState((state) => {
                    return {endYear: state.endYear + 1}
                });
                break;
            default:
                break;
        }
    };

    _resetState = () => {
        // console.log('state', this.state);
        this.setState((state) => {
            const {oldStartDate, oldEndDate} = state;
            return {
                startYear: this._getYearFromStr(oldStartDate),
                endYear: this._getYearFromStr(oldEndDate),
                startMonth: this._getMonthFromStr(oldStartDate),
                endMonth: this._getMonthFromStr(oldEndDate)
            }});
        // console.log('after reset state', this.state);
    };

    _confirmClose = () => {
        this.setState({ confirmOpen: false });
    };

    _selectMonth = (index, type) => {
        switch (type) {
            case 'start':
                this.setState({
                    startMonth: index,
                    startCalendarVisible: false
                });
                break;
            case 'end':
                this.setState({
                    endMonth: index,
                    endCalendarVisible: false
                });
                break;
            default:
                break;
        };
    };

    onSubmit = () => {
        // console.log("local on submit");
        const { startYear, startMonth, endYear, endMonth } = this.state;
        if(startYear > endYear) {
            this.setState({ confirmOpen: true });
            this._resetState();
        }else
            if(startYear === endYear && startMonth > endMonth) {
                this.setState({ confirmOpen: true });
                this._resetState();
            } else {
                const from = startMonth < 10 ? `${startYear}-0${startMonth}` : `${startYear}-${startMonth}`;
                const to = endMonth < 10 ? `${endYear}-0${endMonth}` : `${endYear}-${endMonth}`;
                this.props.onSubmit(from, to);
            }
    };

    _renderCalendar = (month, year, type) => {
        let {monthArray} = this.state;
        let rightChevron = year < +new Date().getFullYear() ?
            <span className="next link" onClick={() => this._onNextYear(type)}>
                <i className="chevron right icon"> </i>
            </span> : null;
        let head =
            <tr style={{width: '100%'}}>
                <th className="left aligned th-prev">
                    <span className="prev link" onClick={() => this._onPrevYear(type)}>
                        <i className="chevron left icon"> </i>
                    </span>
                </th>
                <th className="center aligned th-center">
                    <span className="link ">{year}</span>
                </th>
                <th className="right aligned th-next">
                    {rightChevron}
                </th>
            </tr>;

        let rows = [];
        let curMonth = new Date().getMonth() + 1;
        let curYear = + new Date().getFullYear();
        for(let i = 0; i < monthArray.length; i = i+3 ) {
            let style1 = month === i+1 ? 'link active' : 'link';
            style1 += (curMonth < i+1 && year === curYear) ? ' disabled' : '';
            let style2 = month === i+2 ? "link active" : "link";
            style2 += (curMonth < i+2 && year === curYear) ? ' disabled' : '';
            let style3 = month === i+3 ? "link active" : "link";
            style3 += (curMonth < i+3 && year === curYear) ? ' disabled' : '';

            rows.push(
                <tr key={`tr_${i}`}>
                    <td key={i+1} className={style1} onClick={()=>this._selectMonth(i+1, type)}>{monthArray[i]}</td>
                    <td key={i+2} className={style2} onClick={()=>this._selectMonth(i+2, type)}>{monthArray[i+1]}</td>
                    <td key={i+3} className={style3} onClick={()=>this._selectMonth(i+3, type)}>{monthArray[i+2]}</td>
                </tr>
            );
        }

        const calendar =
            <table className="ui celled center aligned unstackable table three column  ">
                <thead>
                {head}
                </thead>
                <tbody>
                {rows}
                </tbody>
            </table>;
        return calendar;
    };

    render() {
        // console.log(`props new old StartDate ${this.state.oldStartDate}`);

        const {startYear, startMonth, endMonth, endYear, startCalendarVisible, endCalendarVisible} = this.state;

        const startVisibility = startCalendarVisible ? 'visible' : 'hidden';
        const endVisibility = endCalendarVisible ? 'visible' : 'hidden';
        const dimmerVisibility = (startCalendarVisible || endCalendarVisible) ? 'visible' : 'hidden';

        return (
            <Fragment>
                <div style={{display: 'flex', marginLeft: 'auto'}}>
                    <div>
                        <div style={{marginRight: "10px"}}>
                            <div className="ui left icon input my-input">
                                <input type="text"
                                       readOnly
                                       onClick = {() => this._toggleCalendar('start')}
                                       value={`${startMonth}-${startYear}`}
                                       />
                                <i className="calendar alternate outline icon"></i>
                            </div>
                            <div className={ `ui popup my-calendar bottom left flowing transition ${startVisibility}` }>
                                {this._renderCalendar(startMonth,startYear,'start' )}
                            </div>
                        </div>
                    </div>
                    <div>
                        <div style={{marginRight: "10px"}}>
                            <div className="ui left icon input my-input">
                                <input type="text" readOnly onClick={()=>this._toggleCalendar('end')} value={`${endMonth}-${endYear}`}/>
                                <i className="calendar alternate outline icon"></i>
                            </div>
                            <div className={ `ui popup my-calendar bottom center flowing transition ${endVisibility}` }>
                                {this._renderCalendar(endMonth,endYear,'end' )}
                            </div>
                        </div>
                    </div>
                    <button className="ui primary basic button"
                            style={{marginRight: "10px"}}
                            onClick={this.onSubmit}
                    >Показать</button>
                    <button className="ui secondary basic button"
                            onClick={()=>this._resetState()}
                    >Сбросить</button>
                    <div className={`my-dimmer transition ${dimmerVisibility}` } onClick={() => this._toggleCalendar('all')}></div>
                </div>
                <Confirm open={this.state.confirmOpen}
                         header='Обновление не может быть выполнено!'
                         content='Начальная дата должна быть меньше конечной'
                         onConfirm={this._confirmClose}
                         onCancel={this._confirmClose}/>
            </Fragment>
        );
    };
};
