import React, { Component } from 'react';
import { Message } from 'semantic-ui-react';
import './alert.css';
import ErrorBoundry from "../error-boundry";

class Alert extends Component {
    state = { visible: true };

    handleDismiss = () => {
        this.setState({ visible: false });
    };

    // handleClick = ( id, redirectId, type) => {
    //   console.log(`click on ${id} with tedirect id ${redirectId}, type ${type}`);
    //   this.props.handleRedirectByAlert(id, redirectId, type);
    // };

    render() {
        let type = '';

        switch (this.props.type) {
            case 'warning':
                type = 'warning';
                break;
            case 'success':
                type = 'positive';
                break;
            case 'error':
                type = 'negative';
                break;
            case 'info':
                type = 'info';
                break;
            default:
                type = 'info';
        };

        if (this.state.visible) {
            return (
                <ErrorBoundry>
                    <Message
                        onDismiss={this.handleDismiss}
                        className={`custom-alert ${type}`} >
                        <div className={"alert-content"}>
                            <div>
                                <i className="share icon" onClick={this.props.handleClick}></i>
                            </div>
                            <div>
                                <Message.Header>{this.props.header}</Message.Header>
                                <p>{this.props.content}</p>
                            </div>
                        </div>
                    </Message>
                </ErrorBoundry>
            )
        }

        return null;
    }
}

export default Alert;