import React from 'react';
import ChartTemplate from "./chart-template";

import 'semantic-ui-css/semantic.min.css';
import ErrorBoundry from "../error-boundry";
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";

const _constructor = (id, oxName, oyName, ch_series, lineName ) => {
    const divId = `chartdiv_${id}`;

    // Create chart instance
    const chart = am4core.create(`${divId}`, am4charts.XYChart);

    chart.hiddenState.properties.opacity = 0;

    chart.data = ch_series.data;

    chart.colors.step = 2;
    chart.padding(30, 30, 10, 30);

    let categoryAxis = chart.xAxes.push(new am4charts.CategoryAxis());
    categoryAxis.dataFields.category = "date";
    categoryAxis.renderer.grid.template.location = 0;

    let valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
    valueAxis.min = 0;
    valueAxis.max = 100;
    valueAxis.title.text = `${oyName}`;
    valueAxis.baseUnit = " %";
    valueAxis.strictMinMax = true;
    // valueAxis.calculateTotals = true;
    valueAxis.renderer.minWidth = 50;

    // Add scrollbar
    let scrollbarX = new am4charts.XYChartScrollbar();

    ch_series.titles.forEach(function (item) {
        let series = chart.series.push(new am4charts.ColumnSeries());
        series.dataFields.valueY = `${item.value}`;
        series.dataFields.categoryX = "date";
        series.name = `${item.title}`;
        series.columns.template.tooltipText = "{name}: [bold]{valueY}[/]";
        series.tooltip.autoTextColor = false;
        series.tooltip.label.fill = am4core.color("#FFFFFF");
        series.stacked = true;
        series.columns.template.width = am4core.percent(80);

        let bullet = series.bullets.push(new am4charts.LabelBullet());
        bullet.interactionsEnabled = false;
        bullet.label.text = "{valueY}";
        bullet.label.fill = am4core.color("#fff");
        // bullet.label.horizontalCenter = "left";
        bullet.locationY = 0.5;
    });

    const lineAxis = chart.yAxes.push(new am4charts.ValueAxis());
    lineAxis.title.text = `${lineName}`;
    // lineAxis.renderer.grid.template.disabled = true;
    lineAxis.renderer.opposite = true;
    lineAxis.renderer.line.strokeOpacity = 1;
    lineAxis.renderer.line.strokeWidth = 2;
    lineAxis.renderer.line.stroke = am4core.color("#2b4779");

    let lineSeries = chart.series.push(new am4charts.LineSeries());
    lineSeries.name = `${lineName}`;
    lineSeries.dataFields.valueY = "lineY";
    lineSeries.dataFields.categoryX = "date";
    lineSeries.yAxis = lineAxis;
    lineSeries.stroke = am4core.color("#2b4779");
    lineSeries.strokeWidth = 2;
    lineSeries.tooltipText = "[#fff font-size: 15px]{name} за {categoryX}:\n[/][#fff font-size: 20px]{valueY}[/]";
    lineSeries.tooltip.getFillFromObject = false;
    lineSeries.tooltip.background.fill = am4core.color("#6c6c6c");
    lineSeries.tooltip.label.textAlign = "middle";

    let lineBullet = lineSeries.bullets.push(new am4charts.Bullet());
    // bullet.fill = am4core.color("#6c6c6c"); // tooltips grab fill from parent by default
    // bullet.tooltipText = "[#fff font-size: 15px]{name} за {categoryX}:\n[/][#fff font-size: 20px]{valueY}[/]";
    let circle = lineBullet.createChild(am4core.Circle);
    circle.radius = 4;
    circle.fill = am4core.color("#fff");
    circle.strokeWidth = 2;

    //Event listener
    chart.events.on('ready', ()=>{
        const curtain = document.getElementById("curtain");
        curtain.parentElement.removeChild(curtain);
    });

    // Add legend
    chart.legend = new am4charts.Legend();

    // Add cursor
    chart.cursor = new am4charts.XYCursor();

    return chart;
};

const ColumnLineMixChart  = ({ id, oxName, oyName, ch_series, title, lineName, from, to, nodeId }) => {
    return (
        <ErrorBoundry>
            <ChartTemplate chartConstructor={_constructor} id={id} nodeId={nodeId} title={title} oxName={oxName} oyName={oyName} lineName={lineName} ch_series={ch_series} from={from} to={to}/>
        </ErrorBoundry>
    );
};

export default ColumnLineMixChart;