import React from 'react';
import ChartTemplate from "./chart-template";

import 'semantic-ui-css/semantic.min.css';
import ErrorBoundry from "../error-boundry";
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";

const _constructor = (id, oxName, oyName, ch_series ) => {
    const divId = `chartdiv_${id}`;

    // Create chart instance
    const chart = am4core.create(`${divId}`, am4charts.XYChart);

    // Create axes
    const categoryAxis = chart.xAxes.push(new am4charts.CategoryAxis());
    categoryAxis.dataFields.category = "category";
    categoryAxis.renderer.grid.template.location = 0;
    categoryAxis.renderer.minGridDistance = 30;
    categoryAxis.title.text = `${oxName}`;

    // Create value axis
    const valueAxis = chart.yAxes.push(new am4charts.ValueAxis());
    valueAxis.title.text = `${oyName}`;
    // valueAxis.baseUnit = " %";

    // Add scrollbar
    // let scrollbarX = new am4charts.XYChartScrollbar();

    chart.data = ch_series.data;

    // Create series
    const series = chart.series.push(new am4charts.ColumnSeries());
    series.dataFields.valueY = "value";
    series.dataFields.categoryX = "category";
    series.name = `${oyName}`;
    series.columns.template.tooltipText = "{categoryX}: [bold]{valueY}[/] %";
    series.columns.template.fillOpacity = .8;

    let columnTemplate = series.columns.template;
    columnTemplate.strokeWidth = 2;
    columnTemplate.strokeOpacity = 1;

    // scrollbarX.series.push(series);

    // chart.scrollbarX = scrollbarX;

    // Add cursor
    chart.cursor = new am4charts.XYCursor();

    // Add legend
    // chart.legend = new am4charts.Legend();

    //Event listener
    chart.events.on('ready', ()=>{
        const curtain = document.getElementById("curtain");
        curtain.parentElement.removeChild(curtain);
    });

    return chart;
};

const SimpleColumnChart  = ({ id, oxName, oyName, ch_series, title, from, to, nodeId }) => {
    return (
        <ErrorBoundry>
            <ChartTemplate chartConstructor={_constructor} id={id} nodeId={nodeId} title={title} oxName={oxName} oyName={oyName} ch_series={ch_series} from={from} to={to}/>
        </ErrorBoundry>
    );
};

export default SimpleColumnChart;