import React, {Component, Fragment} from 'react';

import 'semantic-ui-css/semantic.min.css';
import './classes-page.css';
import ApiService from "../../services/api-service";
import VerticalTabular from "../tabs/vartical-tabular";
import {Segment, Tab} from "semantic-ui-react";
import ErrorBoundry from "../error-boundry";
import {AccordionList} from "../accordion";


export default class ClassesPage extends Component {

    state = {
        data: [],
        loading: false,
        error: false
    };

    apiService = new ApiService();

    componentDidUpdate(prevProps) {
        if (this.props.nodeId !== prevProps.nodeId) {
            this.update();
        }
    }

    componentDidMount() {
        console.log("componentDidMount");
        this.update();
    }

    update() {
        this.setState( {
            loading: true,
            error: false
        });

        this.apiService.getClassesList(this.props.nodeId)
            .then((data) => {
                console.log(data);
                this.setState({
                    data,
                    loading: false
                });
            })
            .catch((error) => {
                console.log("catch error", error);
                this.setState({
                    data: [],
                    error: true,
                    loading: false
                });
            });
    };

    render() {
        const { data, loading } = this.state;
        const suf = 'class';
        const panes = data.map((item, index) => {
            const {id, title} = item;
            return (
                {
                    menuItem:
                        { key: `${suf}_${id}`, content: title },
                    render: () => {
                        return(
                            <Tab.Pane
                                suf={suf}
                                className="list-tab-pane"
                                attached={false}>
                                <AccordionList getList={this.apiService.getClass}
                                               nodeId={this.props.nodeId}
                                               renderActiveOnly={false}
                                               suf={suf}
                                               tabId={id}
                                />
                            </Tab.Pane>
                        );
                    }
                }
            );
        });

        return (
            <Segment loading={loading} className="vertical-tab-wrapper">
                <Tab
                    menu={{fluid: true, vertical: true, tabular: 'left'}}
                    panes={panes}
                    renderActiveOnly={true}>
                </Tab>
            </Segment>
        )
    };
};
