import React from 'react';
import ChartTemplate from "./chart-template";

import 'semantic-ui-css/semantic.min.css';
import ErrorBoundry from "../error-boundry";
import * as am4core from "@amcharts/amcharts4/core";
import * as am4charts from "@amcharts/amcharts4/charts";

const _constructor = (id, oxName, oyName, ch_series ) => {
    const divId = `chartdiv_${id}`;

    // Create chart instance
    const chart = am4core.create(`${divId}`, am4charts.PieChart);

    // Add data
    chart.data = ch_series.data;

    // Add label
    chart.innerRadius = 50;

    // Add and configure Series
    const pieSeries = chart.series.push(new am4charts.PieSeries());
    pieSeries.dataFields.value = "size";
    pieSeries.dataFields.category = "sector";
    pieSeries.slices.template.configField = "config";

    // Disable ticks and labels
    // pieSeries.labels.template.disabled = true;
    // pieSeries.ticks.template.disabled = true;

    // Disable tooltips
    // pieSeries.slices.template.tooltipText = "";

    // Add a legend
    chart.legend = new am4charts.Legend();

    //Event listener
    chart.events.on('ready', ()=>{
        const curtain = document.getElementById("curtain");
        curtain.parentElement.removeChild(curtain);
    });

    return chart;
};

const PieChart  = ({ id, oxName, oyName, ch_series, title, from, to , nodeId }) => {
    return (
        <ErrorBoundry>
            <ChartTemplate chartConstructor={_constructor} id={id} nodeId={nodeId} title={title} oxName={oxName} oyName={oyName} ch_series={ch_series} from={from} to={to}/>
        </ErrorBoundry>
    );
};

export default PieChart;