import React, {Component, Fragment} from 'react';
import './alerts-list.css';
import 'semantic-ui-css/semantic.min.css';
import Alert from "./alert";
import { Segment } from 'semantic-ui-react';
import ErrorBoundry from "../error-boundry";


export default class AlertsList extends Component {

    render() {
        const arr = this.props.arr || [];
        const elements = arr.map( (item, index) => {
            const {id, header, info, type, redirect_id, redirect_type} = item;
                return(
                    <Alert key={`alert_${id}`}
                           id={id}
                           header={header}
                           content={info}
                           type={type}
                           redirectId={redirect_id}
                           redirectType={redirect_type}
                           handleClick={()=>this.props.handleClick(id, redirect_id, redirect_type)}
                    />
                );
            });

        return (
            <ErrorBoundry>
                <Segment raised  className="alerts-list-wrapper " loading={this.props.loading}>
                    <h5 className="alerts-list-header ui header attached top">
                        Уведомления
                    </h5>
                    <div className="alerts-list-content">
                        <ul>
                            {elements}
                        </ul>
                    </div>
                </Segment>
            </ErrorBoundry>
        );
    };
}
