import React, { Component } from 'react';
import ChartsStack from '../charts-stack';
import TreeView from '../tree-view';
import {TabPointing } from "../tabs";
import ErrorBoundry from "../error-boundry";
import ApiService from "../../services/api-service";
import AlertsList from "../alerts";
import {Tab} from "semantic-ui-react";
import {DepartmentsPage, ClassesPage, StatisticPage} from '../pages';

import './app.css';


export default class App extends Component {

    state = {
        // charts: [],
        alerts: [],
        selectedNode: null,
        redirectId: null,
        // loadingCharts: false,
        loadingAlerts: false
    };

    apiService = new ApiService();

    componentDidMount() { }

    onSelectTreeNode = (node) => {
        const {redirectId} = this.state;
        this.setState({
            selectedNode: node
        });
        if(redirectId) this.setState({
            redirectId: null
        });
        this.updateNodeData(node.id);
    };

    updateNodeData = (id) => {
        this.setState({ loadingCharts: true, loadingAlerts: true, alerts: [], charts: [] });
        // this.apiService.getStatisticCharts(id)
        //     .then((charts) => {
        //         this.setState({
        //             charts,
        //             loadingCharts: false
        //         })
        //     })
        //     .catch((error)=>{
        //         this.setState({
        //             charts: [],
        //             loadingCharts: false
        //         })
        //     });
        this.apiService.getAlerts(id)
            .then((alerts) => {
                this.setState({
                    alerts,
                    loadingAlerts: false
                })
            })
            .catch((error)=>{
                this.setState({
                    alerts: [],
                    loadingAlerts: false
                })
            });
    };

    handleRedirectByAlert = (id, redirectId, redirectType) => {
        this.setState({
            redirectId
        });
    };

  render() {
      const {loadingAlerts, alerts, selectedNode, redirectId } = this.state;

      const nodeId = selectedNode ? selectedNode.id : null;

      const panesDefault = [
          {  menuItem: { key: 'statistics', icon: 'pie graph', content: 'Статистика' },
              render: () => <Tab.Pane attached={false} className="custom-tab"><ChartsStack className='charts-stack' nodeId={nodeId} type={'stat'}/></Tab.Pane>
          }
      ];
      const panesSchool = [
          {  menuItem: { key: 'statistics', icon: 'pie graph', content: 'Статистика' },
              render: () => <Tab.Pane attached={false} className="custom-tab"><ChartsStack className='charts-stack' nodeId={nodeId} secId={null} thId={null} type={'stat'}/></Tab.Pane>
          },
          { menuItem: { key: 'teachers', icon: 'users', content: 'Учителя' },
              render: () => <Tab.Pane attached={false} className="custom-tab">
                                <DepartmentsPage nodeId={nodeId}/>
                            </Tab.Pane>
          },
          { menuItem: { key: 'students', icon: 'child', content: 'Ученики' },
              render: () => <Tab.Pane attached={false} className="custom-tab">
                                  <ClassesPage nodeId={nodeId}/>
                              </Tab.Pane>
          },
      ];

      const panes = (selectedNode && selectedNode.type === 'school') ? panesSchool : panesDefault;
      console.log("app panes", panes);
      return (
          <ErrorBoundry>
              <div className='app-wrapper'>
                  <div className='left-column'>
                      <div className='tree-wrapper'>
                          <TreeView className='tree-view'
                                    onSelect={this.onSelectTreeNode}
                                    redirectId={redirectId}
                          />
                      </div>
                      <div className='alerts-wrapper'>
                          <AlertsList arr={alerts} loading={loadingAlerts} handleClick={this.handleRedirectByAlert}/>
                      </div>
                  </div>
                  <div className='right-column'>
                      <div className='tabs-wrapper'>
                        <TabPointing panes={panes}/>
                      </div>
                  </div>
              </div>
          </ErrorBoundry>
      );
  }
}
