import React, {Component} from 'react';
import {ColumnLineMixChart, LineChart, PieChart, RadialChart, SimpleColumnChart, StackedColumnChart} from "../charts";

import 'semantic-ui-css/semantic.min.css';
import './charts-stack.css';
import {Segment} from "semantic-ui-react";
import ErrorBoundry from "../error-boundry";
import ApiService from "../../services/api-service";

export default class ChartsStack extends Component {
    state={
        data: [],
        loading: false,
        error: false
    };

    apiService = new ApiService();

    componentDidUpdate(prevProps) {
        if (this.props  !== prevProps ) {
            console.log("ChartsStack update props");
            this.update();
        }
    }

    componentDidMount() {
        console.log(" ChartsStack componentDidMount");
        this.update();
    }

    update() {
        const {nodeId, secId, thId, type} = this.props;

        let url = ``;
        switch (type) {
            case 'stat':
                url = `/${nodeId}/statistic`;
                break;
            case 'dep':
                url = `/${nodeId}/departments/${secId}/teachers/${thId}/charts`;
                break;
            case 'class':
                url = `/${nodeId}/classes/${secId}/students/${thId}/charts`;
                break;
            default:
                break;
        };

        this.setState( {
            loading: true,
            error: false,
            data: []
        });

        this.apiService.getCharts(url)
            .then((data) => {
                console.log(data);
                this.setState({
                    data,
                    loading: false
                });
            })
            .catch((error) => {
                console.log("catch error", error);
                this.setState({
                    data: [],
                    error: true,
                    loading: false
                });
            });
    };


    _renderElements = (data, nodeId) => {
        return data.map((item) => {
            const {id, title, oxName, oyName, series, type, lineName, from, to} = item;
            switch (type) {
                case 'line':
                    return (
                        <LineChart key={id} nodeId={nodeId} id={id} title={title} oxName={oxName} oyName={oyName}
                                   ch_series={series} from={from} to={to}/>
                    );
                case 'pie':
                    return (
                        <PieChart key={id} nodeId={2} id={id} title={title} ch_series={series} from={from} to={to}/>
                    );
                case 'stacked-column':
                    return (
                        <StackedColumnChart key={id} nodeId={nodeId} id={id} title={title} ch_series={series}
                                            from={from} to={to}/>
                    );
                case 'radial':
                    return (
                        <RadialChart key={id} nodeId={nodeId} id={id} title={title} ch_series={series} from={from}
                                     to={to}/>
                    );
                case 'simple-column':
                    return (
                        <SimpleColumnChart key={id} nodeId={nodeId} id={id} title={title} oxName={oxName}
                                           oyName={oyName} ch_series={series} from={from} to={to}/>
                    );
                case 'column-line-mix':
                    return (
                        <ColumnLineMixChart key={id} nodeId={nodeId} id={id} title={title} oxName={oxName}
                                            oyName={oyName} lineName={lineName} ch_series={series} from={from} to={to}/>
                    );
                default:
                    return null;

            }
        });
    };

    render() {
        const {data, loading} = this.state;

        return(
            <ErrorBoundry>
                <Segment className="charts-stack-wrapper"  loading={loading}>
                    {this._renderElements(data, this.props.nodeId)}
                </Segment>
            </ErrorBoundry>
        );
    };
};